img {
  object-fit: cover;
  height: auto;
  width: 700px;
  max-height: 100%;
  max-width: 100%;
  border: 5px solid #2e8b57;
  border-radius: 10px;
  margin-top: 20px;
  box-shadow: 0 0 15px;
}

select {
  width: 100%;
  max-width: 300px;
  height: 50px;
  color: #0c5437;
  background-color: #ff4500;
  border: 2px solid red;
  border-radius: 30px;
  padding: 10px 15px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  transition: background-color .3s, box-shadow .3s, border-color .3s;
  position: relative;
}

select:hover {
  background-color: #18dff1;
  border-color: #0a5989;
  box-shadow: 0 0 15px rgba(174, 193, 33, .843);
}

.error {
  margin: 0;
  padding-top: 20px;
  font-size: 30px;
}

.secondwrapper {
  margin: 0;
}

.cat-info {
  max-width: 800px;
  background-color: #d1f7f2;
  border: 2px solid #d0e3d0;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 15px rgba(150, 48, 48, .1);
}

.cat-info img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 20px;
}

.cat-info p {
  color: #1e1569;
  margin: 5px 0;
  font-size: 18px;
}

.cat-info p strong {
  color: #00f;
}

.wrapper {
  gap: 50px;
  display: flex;
}

.loader {
  width: fit-content;
  background: linear-gradient(currentColor 0, currentColor 0) 0 100% / 0% 3px no-repeat;
  margin: 0;
  padding-top: 5px;
  padding-bottom: 8px;
  font-family: sans-serif;
  font-size: 30px;
  font-weight: bold;
  animation: l2 2s linear infinite;
}

.loader:before {
  content: "Loading...";
}

@keyframes l2 {
  to {
    background-size: 100% 3px;
  }
}

